	module.exports = {"data":{"post":{"html":"<p>We're in the middle of a hard month here. Mama has multi-night business trips every week in March (normal is one per month), and that makes neither of us happy. And Christopher least of all. So tonight, when Mama returned from 3 days in Munich, we together with Oma celebrated with a trip to the bistro, with <em>Apfelschorle</em> (apple juice spritzer), pizza, and ice cream. Actually, Christopher calls all golden-colored fizzy beverages <em>Apfelschorle</em>, so Mama and I took the beer <em>Apfelschorle</em> and let him take the juice <em>Apfelschorle</em>. And Christopher was struggling to keep his eyes open after the pizza, so we decided to save the ice cream for tomorrow. On the way home, Christopher kept muttering \"Tolle Apfelschorle\" (toll=super or great), so I guess he enjoyed himself. And I'll wait until at least tomorrow to write anything more about international politics.</p>","fields":{"title":"Here We Are","date":"14 Mar 2003","url":"https://papascott.de/2003-03-14-here-we-are/"}}},"pathContext":{"slug":"/2003-03-14-here-we-are/","prev":{"fields":{"title":"Freedom Tofu","date":"13 Mar 2003","slug":"/2003-03-13-freedom-tofu/"}},"next":{"fields":{"title":"Wag Chirac","date":"15 Mar 2003","slug":"/2003-03-15-wag-chirac/"}}}}

